h1, h2, h3 {
  margin: 0;
}

.page {
  color: #161616;
  scroll-behavior: smooth;
  font-family: Cinzel, serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
}

.page__body {
  margin: 0;
}

.page__body--with-menu {
  overflow-y: hidden;
}

.page__menu {
  transition: transform .6s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  margin-bottom: 100px;
}

@media (width >= 744px) {
  .page__section {
    margin-bottom: 140px;
  }
}

.page__section-title {
  text-align: center;
  letter-spacing: -1.5px;
  color: #161616;
  margin-bottom: 48px;
  font-size: 32px;
  font-weight: 700;
  line-height: 32px;
}

@media (width >= 744px) {
  .page__section-title {
    font-size: 48px;
    line-height: 48px;
  }
}

.grid {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width >= 744px) {
  .grid {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }
}

@media (width >= 1020px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
