.header {
  background-image: url("header.2b2210af.png");
  background-position: center;
  background-size: cover;
}

.header__content {
  box-sizing: border-box;
  -ms-flex-direction: column;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  padding: 24px 0 288px;
  display: -ms-flexbox;
  display: flex;
}

@media (width >= 744px) {
  .header__content {
    padding: 32px 0 456px;
  }
}

@media (width >= 1020px) {
  .header__content {
    padding: 32px 0 480px;
  }
}

.header__title {
  text-align: center;
  color: #fff;
  letter-spacing: -1.5px;
  margin: 0;
  font-size: 40px;
  font-weight: 700;
  line-height: 100%;
}

@media (width >= 744px) {
  .header__title {
    font-size: 64px;
  }
}

@media (width >= 1020px) {
  .header__title {
    font-size: 80px;
  }
}

.header__call {
  text-decoration: none;
}

@media (width >= 1020px) {
  .header__call:before {
    content: "The Met Fifth Avenue +1 212-535-7710";
    text-transform: uppercase;
    color: #fff;
    opacity: 0;
    pointer-events: none;
    width: 201px;
    padding-right: 20px;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    display: block;
    transform: translate(-100%, 20%);
  }

  .header__call:not(:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi))):before {
    text-align: right;
  }

  .header__call:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)):before {
    text-align: left;
  }

  .header__call:after {
    content: "The Met Cloisters +1 212-923-3700";
    text-transform: uppercase;
    color: #fff;
    opacity: 0;
    pointer-events: none;
    width: 172px;
    padding-right: 20px;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    display: block;
    transform: translate(-100%, 40%);
  }

  .header__call:not(:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi))):after {
    text-align: right;
  }

  .header__call:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)):after {
